import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm, Field } from "redux-form";
import { compose } from "redux";
import PropTypes from "prop-types";
import {
  Avatar,
  Button,
  Card,
  Container,
  CssBaseline,
  TextField,
  Typography,
} from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import { LockOpenOutlined } from "@material-ui/icons";
import * as actions from "../../actions";

const useStyles = (theme) => ({
  root: {
    maxWidth: 400,
    minWidth: 375,
    marginTop: "66px",
    alignItems: "center",
  },

  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  radio: {
    marginTop: theme.spacing(2),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.primary.main,
  },
  form: {
    width: "355px", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },

  bg: {
    height: "90vh",
    background: "none",
    backgroundImage:
      "url(" +
      "https://images.unsplash.com/photo-1494949649109-ecfc3b8c35df?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1189&q=80" +
      ")",
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
  },
});
class LoginPage extends Component {
  managmentsubmit = (formProps) => {
    this.props.signin(formProps, () => {
      this.props.history.push("/management");
    });
  };

  managementEmail = ({ input, label, ...custom }) => (
    <TextField
      label="Email"
      {...input}
      {...custom}
      variant="filled"
      margin="normal"
    />
  );
  managementPassword = ({ input, label, ...custom }) => (
    <TextField
      label="Password"
      {...input}
      {...custom}
      variant="filled"
      margin="normal"
    />
  );

  render() {
    const { handleSubmit } = this.props;
    const { classes } = this.props;

    return (
      <div className={classes.bg}>
        <Container component="main" maxWidth="xs">
          <div>
            <Card className={classes.root}>
              <CssBaseline />
              <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                  <LockOpenOutlined />
                </Avatar>

                <Typography component="h5" variant="h5">
                  Welcome To Login Panel
                </Typography>
                <div>
                  <form
                    onSubmit={handleSubmit(this.managmentsubmit)}
                    className={classes.form}
                    id="managementform"
                  >
                    <Field
                      autoComplete="off"
                      required
                      fullWidth
                      name="email"
                      type="email"
                      component={this.managementEmail}
                    />
                    <br />
                    <div style={{ color: "red" }}>
                      {this.props.errorMessage}
                    </div>
                    <Field
                      autoComplete="off"
                      required
                      fullWidth
                      name="password"
                      type="password"
                      component={this.managementPassword}
                    />

                    <Button
                      type="submit"
                      fullWidth
                      variant="contained"
                      color="primary"
                      className={classes.submit}
                    >
                      Managment Login
                    </Button>
                  </form>
                </div>
              </div>
            </Card>
          </div>
        </Container>
      </div>
    );
  }
}
LoginPage.propTypes = {
  classes: PropTypes.object,
};
function mapStateToProps(state) {
  return {
    errorMessage: state.auth.errorMessage,
  };
}

export default compose(
  connect(mapStateToProps, actions),
  withStyles(useStyles),
  reduxForm({ form: "login" })
)(LoginPage);
