import { Button } from "@material-ui/core";
import React from "react";
import { useHistory } from "react-router";

const LoginOptions = () => {
  const history = useHistory();

  const Loginmanagement = () => {
    history.push("/loginpage");
  };

  const Loginadmin = () => {
    history.push("/adminlogin");
  };

  return (
    <div style={{ marginTop: "79px", textAlign: "center" }}>
      <Button color="primary" variant="contained" onClick={Loginmanagement}>
        login as management
      </Button>
      <Button color="secondary" variant="contained" onClick={Loginadmin}>
        login as admin
      </Button>
      <Button color="inherit" variant="contained">
        login as teachers
      </Button>
    </div>
  );
};

export default LoginOptions;
